body{
  font-size: 20px;
  font-weight: bold;
}
.container-wrapper{
  background-color: rgb(42, 19, 63);
}
.container{
  background-color: rgb(255,190,64);
  width: 50%;
  margin-right: auto;
  margin-left: auto;
}
.title{
  margin-top: 20px;
  font-size: 30px;
  margin-bottom: 0px;
}
.question{
  font-size: 25px;
}
.container, .night-container, .genre-container, .actors-container, 
.date-container, .rating-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

input[type="checkbox"]{
  margin-right:20px;  
  align-items:center;
  vertical-align: middle;
  accent-color:purple;
  filter: sepia(100%) brightness(80%) hue-rotate(170deg) saturate(70%) contrast(300%);
}

.night-container{
  margin-top: 50px;
  vertical-align: middle;
}

.box{
  margin-top:30px;
  margin-bottom: 20px;
}

